import { Link } from "gatsby"


const NotFoundPage = () => {
  return (
    <>
      <div className="bg-image-wrapper">
        <StaticImage className="bg-image" loading="eager" src="../images/forest_colorized.jpg" alt="" layout="fullWidth" />
        <div className="container padding-y-3 flex-column-center-start gap-m white" >
          <h1 className="page-title">404</h1>
          <Hr />
          <p className="page-subtitle">page introuvable</p>
        </div>
         <Delimiter absolute bottom right /> 

      </div>
      <section className="container padding-y-3">
        <Link to="/" className="button">Retour Ã  l'accueil</Link>

      </section >
      <Footer />
    </>
  )
}

export default NotFoundPage

export const Head = () => <title>Not found</title>
